@import "../../inc/vars";
.acro-industry-hero {
	padding: 60px 0;
	text-align: center;
	display: flex;
	flex-wrap: wrap;
	background: #FBF6FF;
	align-items: center;
	width: 1400px;
	max-width: 100%;
	margin: 0 auto;
	@media (min-width: $screen-sm) {
		text-align: left;
		padding: 60px 30px;
	} 
	&__content {
		width: 100%;
		margin-bottom: 16px;
		padding: 0 18px;
		@media (min-width: $screen-sm){
			width: 62%;
			margin-bottom: 0;
		}
		&__breadcrumbs {
			color: $purple;
			@include pop(500, 20, 1.36);
			margin-bottom: 16px;
			.breadCrumb2 {
				@include pop(500, 26, 1.24);
				@media (min-width: $screen-sm){
					@include pop(500, 20, 1.36);
				}
			}
			.breadCrumb1 {
				display: block;
				margin-bottom: 16px;
				@media (min-width: $screen-sm){
					padding-right: 36px;
					position: relative;
					display: inline-block;
					margin-bottom: 0;
					&:after {
						content: '';
						display: block;
						width: 5px;
						height: 5px;
						border-radius: 50%;
						background: $purple;
						position: absolute;
						right: 16px;
						top: calc(50% - 3px);
					}
				}
			}
		}
		h2, h1 {
			@include titleGrad();
			@include pop(400, 46, 1.24);
			margin-bottom: 16px;
			@media (min-width: $screen-sm){
				font-size: 52px;
			}
		}
		&__description {
			@include fig(400, 18, 1.56);
			color: #4F4B58;
			@media (min-width: $screen-sm){
				font-size: 20px;
			}
		}
	}
	&__image {
		width: 100%;
		position: relative;
		@media (min-width: $screen-sm){
			width: 38%;
			padding-left: 30px;
			spline-viewer {
				height: 400px;
			}
		}
	}
	&_2 {
		text-align: center;
		.acro-industry-hero__content {
			width: 100%;
			margin-bottom: 30px;
			h1 {
				margin-bottom: 20px;
				font-size: 32px;
				@media (min-width: $screen-sm) {
					margin-bottom: 30px;
					font-size: 56px;
				}
			}
			&__breadcrumbs {
				text-align: center;
				.breadCrumb1 {
					&:after {
						display: none;
					}
				}
				.breadCrumb2 {
					display: none;
				}
			}
		}
		.acro-industry-hero__image {
			width: 100%;
			padding: 0;
			spline-viewer {
				height: 500px;
			}
		}
	}
	&_3 {
		.acro-industry-hero__content__breadcrumbs {
			.breadCrumb1 {
				&:after {
					display: none;
				}
			}
			.breadCrumb2 {
				display: none;
			}
		}
		@media (min-width: $screen-sm){
			.acro-industry-hero__content {
				width: 33%;
			}
			.acro-industry-hero__image {
				width: 67%;
				spline-viewer {
					height: 500px;
				}
			}
		}
		spline-viewer {
			height: 500px;
		}
	}
	&_3 {
		.acro-industry-hero__content__breadcrumbs {
			.breadCrumb1 {
				&:after {
					display: none;
				}
			}
			.breadCrumb2 {
				display: none;
			}
		}
		@media (min-width: $screen-sm){
			.acro-industry-hero__content {
				width: 33%;
			}
			.acro-industry-hero__image {
				width: 67%;
				spline-viewer {
					height: 500px;	
				}
			}
		}
	}
}
.wp-block-acrobits-industry-hero {
	.toggle-list {
		border-radius: 0 0 16px 16px;
		box-shadow: 0px 2px 4px 0px rgba(7, 4, 146, 0.10), 0px 24px 60px 0px rgba(6, 47, 125, 0.05), 0px 12px 24px 0px rgba(27, 59, 119, 0.05);
		margin: 0;
		@media (min-width: $screen-sm){
			display: none !important;
		}
	}
}